@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Zen+Loop:ital@1&display=swap');

body {
	font-family: 'Montserrat', sans-serif;
	background: #515e63;
	color: #fff;
}

h1 {
	font-family: 'Zen Loop', cursive;
}

button {
	color: #fff;
	font-family: 'Montserrat', sans-serif;
	background: #515e63;
	cursor: pointer;
}

.note {
	color: #fff;
	padding-top: 5px;
	font-size: 15px;
}

.error {
	color: red;
	background: lightgrey;
	font-size: 20px;
	border-style: solid;
	border-radius: 5px;
	padding: 10px;
	margin-bottom: 10px;
}
